.connect-container {
  width: 100%;
  padding: 100px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: #f7f7f7; /* Nate 的淡灰背景 */
  margin-top: 80px;
}

.connect-title {
  font-family: "Work Sans", sans-serif;
  font-size: 48px;
  font-weight: 600;
  color: #383838;
  margin-bottom: 40px;
}

.connect-btn {
  background: #dd6b5a;
  color: white;
  padding: 18px 48px;
  font-size: 24px;
  font-family: "Work Sans", sans-serif;
  font-weight: 500;
  border-radius: 18px;
  text-decoration: none;
  display: inline-flex;
  align-items: center;
  gap: 12px;
  transition: all 0.2s ease;
}

.connect-btn:hover {
  background: #c85d4f;
  transform: translateY(-2px);
}

.connect-icon {
  background: white;
  color: #dd6b5a;
  font-size: 20px;
  padding: 2px 6px;
  border-radius: 6px;
  font-weight: 700;
}

/* Mobile */
@media (max-width: 768px) {
  .connect-title {
    font-size: 36px;
  }

  .connect-btn {
    font-size: 20px;
    padding: 16px 36px;
  }
}
