.about-container {
  width: 100vw;
  margin-left: calc(50% - 50vw);
  background: #f7f8f9;
  padding: 120px 0;
}

.about-section {
  max-width: 950px;
  margin: 0 auto;
  padding: 0 32px;
}

.about-title {
  font-family: 'Libre Baskerville', serif;
  font-size: 68px;
  font-weight: 700;
  color: #0a214c;
  margin-bottom: 40px;
}

.about-intro {
  font-size: 24px;
  line-height: 1.55;
  color: #3b4d63;
  max-width: 700px;
  margin-bottom: 80px;
}

/* Sections */
.section-heading {
  font-size: 20px;
  font-weight: 600;
  letter-spacing: 0.06em;
  color: #6f7780;
  margin-top: 60px;
  margin-bottom: 28px;
  text-transform: uppercase;
}

/* Blocks */
.edu-block,
.exp-block {
  margin-bottom: 50px;
}

.edu-block h3,
.exp-block h3 {
  font-size: 28px;
  font-weight: 600;
  margin-bottom: 6px;
  color: #0a214c;
}

.edu-sub,
.exp-sub {
  font-size: 18px;
  color: #6f7780;
  margin-bottom: 14px;
}

.edu-list li,
.exp-list li {
  font-size: 20px;
  color: #3b4d63;
  line-height: 1.55;
  margin-bottom: 10px;
}

/* —————— RESPONSIVE —————— */
@media (max-width: 900px) {
  .about-title {
    font-size: 52px;
  }
  .about-intro {
    font-size: 22px;
  }
  .edu-block h3,
  .exp-block h3 {
    font-size: 24px;
  }
}

@media (max-width: 600px) {
  .about-title {
    font-size: 42px;
  }
  .about-intro {
    font-size: 20px;
  }
  .edu-block h3,
  .exp-block h3 {
    font-size: 21px;
  }
  .edu-list li,
  .exp-list li {
    font-size: 18px;
  }
}

/* ==== 白色卡片背景 ==== */
.about-card {
  background: #ffffff;
  border-radius: 28px;
  padding: 60px 70px;
  margin-bottom: 60px;
  box-shadow: 0 8px 28px rgba(0,0,0,0.06);
}

/* ==== 标题（横线 + 文字） ==== */
.section-label {
  display: flex;
  align-items: center;
  margin-bottom: 32px;
}

.section-label .line {
  width: 60px;
  height: 2px;
  background: #999;
  margin-right: 18px;
}

.section-label .label-text {
  font-family: 'Work Sans', sans-serif;
  font-size: 20px;
  letter-spacing: 0.15em;
  color: #666;
}

/* ==== Section 内容 ==== */
.edu-block, .exp-block {
  margin-bottom: 40px;
}

.edu-sub, .exp-sub {
  color: #666;
  font-size: 18px;
  margin-top: 6px;
  margin-bottom: 12px;
}

.edu-list li,
.exp-list li {
  font-size: 18px;
  line-height: 1.6;
  margin-bottom: 8px;
}
