

/* 背景与全局填充 */
.navbar {
  width: 100vw;
  margin-left: calc(50% - 50vw);
  background: #edf9fe;
  padding: 28px 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.nav-left {
  display: flex;
  align-items: center;
  gap: 12px;
}

/* 内部容器 */
.nav-inner {
  max-width: 1400px;
  width: 92%;
  margin: 0 auto;
  flex-wrap: nowrap;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/* LOGO（左侧） */
.nav-logo {
  height: 34px;
  width: auto;
  display: block;
  opacity: 0.9;
}

.nav-name {
  font-family: 'Work Sans', Helvetica, Arial, Lucida, sans-serif;
  font-size: 25px;
  font-weight: 500;
  color: #0a214c;
}

.nav-links {
  list-style: none;
  display: flex;
  gap: 42px; 
  white-space: nowrap; 
  flex-direction: row;
  align-items: center;
}

/* 单个链接 */
.nav-links a {
  font-family: 'Work Sans', sans-serif;
  font-size: 20px;
  font-weight: 400;
  color: #3b4d63;
  text-decoration: none;
  transition: opacity 0.2s ease;
}

.nav-links a:hover {
  opacity: 0.55;
}
