body {
  font-family: 'Work Sans', Helvetica, Arial, sans-serif;
  margin: 0;
  padding: 0;
}

.hero-container {
  width: 100vw;
  margin-left: calc(50% - 50vw);
  background: #EDF9FE;
  padding-top: 20px;
  padding-bottom: 60px;
  padding-left: 10px;
  padding-right: 50px;
  display: flex;
  justify-content: center;
}

.hero-box {
  width: 92%;
  max-width: 1200px;
  margin: 0 auto;
  background: #ffffff;
  border-radius: 32px;
  padding: 70px 80px;
  display: grid;
  grid-template-columns: 1fr 420px;
  align-items: center;
  box-shadow: 0 22px 50px rgba(0,0,0,0.06);
  gap: 20px;     
}

/* Left section */
.hero-left {
  min-width: 0;
  padding-left: 80px;
  max-width: 600px;
}

/* Right section */
.hero-right {
  display: flex;
  justify-content: center;
  
}

/* Illustration */
.hero-img {
  max-width: 420px;
  width: 100%;
  height: auto;
  object-fit: contain;
  user-select: none;
}

/* Top small text */
.hero-topline {
  font-family: 'Work Sans', sans-serif;
  font-size: 22px;
  font-weight: 400;
  letter-spacing: 0.08em;
  color: #6f7780;
  margin-bottom: 28px;
}

/* Main Title --- Use Libre Baskerville (serif) */
.hero-title {
  font-family: 'Libre Baskerville', serif;
  font-size: 100px;
  font-weight: 700;
  color: #0a214c;
  line-height: 1.12;
  margin-bottom: 40px;
}

/* Subtitle --- Work Sans */
.hero-subtitle {
  font-family: 'Work Sans', sans-serif;
  font-size: 30px;
  color: #3b4d63;
  line-height: 1.45;
  max-width: 600px;
}



@media (min-width: 1440px) {
  .hero-content {
    max-width: 1400px;
    gap: 48px;
  }

  .hero-title {
    font-size: 112px;
  }

  .hero-subtitle {
    font-size: 34px;
  }
}

@media (max-width: 1024px) {
  .hero-box {
    grid-template-columns: 1fr;   /* 单列 */
    text-align: center;
    padding: 50px 40px;
  }

  .hero-right {
    order: -1;    /* 图片放上面（Nate 是左文右图，但 mobile 是图在上） */
  }

  .hero-title {
    font-size: 64px;
  }

  .hero-subtitle {
    font-size: 22px;
    margin: 0 auto;
  }
}

/* ------ Mobile: stack text first, image second ------ */
@media (max-width: 900px) {
  .hero-box {
    grid-template-columns: 1fr; /* 纵向一列 */
    text-align: left;
    padding: 32px 20px;
  }

  .hero-left {
    order: 1; /* 文字放在最上 */
    margin-bottom: 32px;
  }

  .hero-right {
    order: 2; /* 图片放在下面 */
    display: flex;
    justify-content: center;
  }

  .hero-img {
    width: 70%;
    max-width: 340px;
    margin: 0 auto;
  }

  .hero-title {
    font-size: 48px;
    line-height: 1.15;
  }

  .hero-subtitle {
    font-size: 20px;
    line-height: 1.45;
  }

  .hero-topline {
    font-size: 18px;
  }
}



@media (max-width: 640px) {
  .hero-box {
    padding: 40px 30px;
    grid-template-columns: 1fr;
    gap: 40px;
  }

  .hero-title {
    font-size: 46px;
    line-height: 1.15;
  }

  .hero-subtitle {
    font-size: 18px;
  }

  .hero-img {
    max-width: 80%;
  }
}

